import React from "react";

const SearchBox = ({ searchChange }) => {
  return (
    <div className="search-box-wrap mb-1">
      <input
        onChange={searchChange}
        type="search"
        placeholder="Search..."
        id="search"
        name="search"
        className="search-box"
      />
    </div>
  );
};

export default SearchBox;
