import React from "react";

const Card = ({ robot }) => {
  return (
    <div className="card">
      <img alt="Robot" src={`https://robohash.org/${robot.username}?200x200`} />
      <div>
        <h3>{robot.name}</h3>
        <p>{robot.email}</p>
      </div>
    </div>
  );
};

export default Card;
